<template>
  <div class="store-manage">
    <div class="item">
      <em>门店编号</em>
      <p>SH168</p>
    </div>
    <div class="item">
      <em>门店名称</em>
      <p>上海华新一店</p>
    </div>
    <div class="item">
      <em>门店地址</em>
      <p>上海市青浦区华志路1788号</p>
    </div>
    <div class="item">
      <em>门店电话</em>
      <span @click="goEditTel">35878745</span>
    </div>
    <div class="item">
      <em>营业时间</em>
      <span @click="goEditDoTime">8:00-21:00</span>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    goEditTel () {
      this.$router.push({ path: '/editTel' })
    },
    goEditDoTime () {
      this.$router.push({ path: '/editDoTime' })
    }
  }
}
</script>

<style lang="stylus" scoped>
.store-manage {
  padding: 30px 20px;
  .item {
    display: flex;
    height: 55px;
    border-bottom: 1px solid #ccc;
    em {
      flex: 1;
      width: 80px;
      line-height: 55px;
    }
    p {
      line-height: 55px;
      font-size: 13px;
      text-align: right;
      color: #666;
    }
    span {
      line-height: 55px;
      padding-right: 25px;
      background: url(../assets/img/right-arrow2.png) no-repeat right center;
      background-size: auto 50%;
    }
  }
}
</style>



